@font-face {
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/lato/Lato-Regular.woff2") format("woff2"), url("../fonts/lato/Lato-Regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswald/oswaldregular.woff2") format("woff2"), url("../fonts/oswald/oswaldregular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/oswald/oswaldmedium.woff2") format("woff2"), url("../fonts/oswald/oswaldmedium.woff") format("woff");
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page__body {
  margin: 0;
  font-family: "Lato", "Arial", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 14px;
  line-height: 18px;
  color: #444444;
}

.header {
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  padding: 10px 0 17px 0;
}

.header__logo {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 57px;
  text-decoration: none;
}

@media (min-width: 768px) {
  .header {
    width: 708px;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding: 24px 0 0 0;
  }

  .header__logo {
    gap: 12px;
  }
}
@media (min-width: 1440px) {
  .header {
    width: 1220px;
    padding: 53px 0 0 0;
  }

  .header__logo {
    gap: 14px;
  }
}
@media (min-width: 768px) {
  .navigation__toggle {
    display: none;
  }
}
.nav-list {
  padding: 0;
  margin: 0;
  list-style-type: none;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
}

.nav-list__item {
  position: relative;
  text-align: center;
}
.nav-list__item::before {
  position: absolute;
  content: "";
  left: 0;
  width: 100%;
  height: 1px;
  background-color: #e6e6e6;
}

.nav-list__link {
  display: block;
  padding: 22px 50px 24px 50px;
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  color: #000000;
  text-transform: uppercase;
  text-decoration: none;
}

@media (min-width: 768px) {
  .nav-list {
    max-width: 480px;
    flex-direction: row;
    gap: 25px;
  }

  .nav-list__link {
    position: relative;
    padding: 10px;
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
  }

  .nav-list__link--current::before {
    position: absolute;
    content: "";
    height: 2px;
    left: 10px;
    right: 10px;
    bottom: 0;
    background-color: #68b738;
  }

  .nav-list__item::before {
    content: none;
  }
}
@media (min-width: 1440px) {
  .nav-list {
    max-width: 680px;
  }

  .nav-list__link {
    font-size: 20px;
    line-height: 30px;
  }

  .nav-list--index-nav-list .nav-list__link {
    color: #ffffff;
  }

  .nav-list--index-nav-list .nav-list__link--current::before {
    background-color: #ffffff;
  }
}
