.application {
  padding: 30px 20px 18px 20px;
  background-color: #f2f2f2;
}

.application__title {
  display: inline-flex;
  align-items: center;
  gap: 19px;
  position: relative;
  margin: 0;
  margin-bottom: 29px;
  font-family: "Oswald", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 37px;
  color: #000000;
  text-transform: uppercase;
}
.application__title::before {
  content: "";
  width: 50px;
  height: 50px;
  background-color: #68b738;
  border-radius: 50%;
}

.application--weight-loss .application__title::after {
  content: "";
  position: absolute;
  width: 36px;
  height: 50px;
  background-image: url("../img/cat-slim.svg");
  left: 7px;
  bottom: 3px;
}

.application--weight-gain .application__title::after {
  content: "";
  position: absolute;
  width: 67px;
  height: 28px;
  background-image: url("../img/cat-pro.svg");
  left: -10px;
  bottom: 11px;
}

.application__description {
  position: relative;
  margin: 0;
  margin-bottom: 38px;
}
.application__description::after {
  position: absolute;
  content: "";
  left: 0;
  bottom: -23px;
  height: 1px;
  width: 100%;
  background-color: #d9d9d9;
}

.application__link {
  display: inline-flex;
  align-items: center;
  position: relative;
  padding: 4px 47px 4px 0;
  font-family: "Oswald", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  text-transform: uppercase;
  text-decoration: none;
  color: #000000;
}
.application__link::after {
  position: absolute;
  content: "";
  right: 8px;
  width: 24px;
  height: 12px;
  background-image: url("../img/arrow.svg");
}

@media (min-width: 768px) {
  .application {
    padding: 77px 316px 53px 52px;
    position: relative;
  }
  .application::before {
    position: absolute;
    content: "";
    right: 62px;
    top: 92px;
    width: 200px;
    height: 200px;
    background-color: #68b738;
    border-radius: 50%;
  }

  .application__title {
    font-size: 36px;
    line-height: 36px;
    margin-bottom: 24px;
  }
  .application__title::before {
    content: none;
  }

  .application--weight-loss .application__title::after {
    content: none;
  }

  .application--weight-gain .application__title::after {
    content: none;
  }

  .application--weight-loss::after {
    position: absolute;
    content: "";
    right: 92px;
    top: 81px;
    width: 140px;
    height: 194px;
    background-image: url("../../img/cat-slim.svg");
    background-repeat: no-repeat;
    background-size: 140px 194px;
  }

  .application--weight-gain::after {
    position: absolute;
    content: "";
    right: 28px;
    top: 135px;
    width: 268px;
    height: 112px;
    background-image: url("../../img/cat-pro.svg");
    background-size: 268px 112px;
  }

  .application__description {
    margin-bottom: 48px;
    font-size: 16px;
    line-height: 24px;
    color: #000000;
  }
  .application__description::after {
    content: none;
  }

  .application__link {
    padding: 4px 57px 4px 0;
    font-size: 20px;
    line-height: 30px;
  }
}
@media (min-width: 1440px) {
  .application {
    box-sizing: border-box;
    padding: 77px 61px 55px 52px;
    width: 570px;
  }
  .application::before {
    content: none;
  }

  .application--weight-loss::after,
.application--weight-gain::after {
    content: none;
  }

  .application--weight-loss .application__title::after {
    content: "";
    left: 15px;
    bottom: 9px;
    width: 70px;
    height: 97px;
    background-size: 70px 97px;
  }

  .application--weight-gain .application__title::after {
    content: "";
    left: -19px;
    bottom: 24px;
    width: 134px;
    height: 56px;
    background-size: 134px 56px;
  }

  .application__title {
    gap: 62px;
    margin-bottom: 39px;
  }
  .application__title::before {
    content: "";
    width: 100px;
    height: 100px;
  }

  .application__description {
    margin-bottom: 23px;
  }
}
